import { createRouter, createWebHistory } from 'vue-router';
import MainPage from '@/pages/MainPage.vue';
import CheckPartner from '@/pages/CheckPartner.vue';
import CreateMain from '@/pages/CreateMain.vue';
import CreateVerify from '@/pages/CreateVerify.vue';
import CreateAdd from '@/pages/CreateAdd.vue';
import CreateDone from '@/pages/CreateDone.vue';
import DeletePhone from '@/pages/DeletePhone.vue';
import DeleteDone from '@/pages/DeleteDone.vue';
import DeleteMain from '@/pages/DeleteMain.vue';
import DeleteVerify from '@/pages/DeleteVerify.vue';
import DeletePartner from '@/pages/DeletePartner.vue';
import DeletePartners from '@/pages/DeletePartners.vue';
import PartnersVerify from '@/pages/PartnersVerify.vue';
import PartnersDone from '@/pages/PartnersDone.vue';

const routes = [
  {
    path: '/',
    name: 'main',
    component: MainPage,
  },
  {
    path: '/check-partner/married',
    name: 'check-partner__married',
    component: CheckPartner,
  },
  {
    path: '/create-couple/main',
    name: 'create-couple__main',
    component: CreateMain,
  },
  {
    path: '/create-couple/verify',
    name: 'create-couple__verify',
    component: CreateVerify,
  },
  {
    path: '/create-couple/add',
    name: 'create-couple__add',
    component: CreateAdd,
  },
  {
    path: '/create-couple/done',
    name: 'create-couple__done',
    component: CreateDone,
  },
  {
    path: '/delete/phone',
    name: 'delete__phone',
    component: DeletePhone,
  },
  {
    path: '/delete/done',
    name: 'delete_done',
    component: DeleteDone,
  },
  {
    path: '/delete/main',
    name: 'delete_main',
    component: DeleteMain,
  },
  {
    path: '/delete/verify',
    name: 'delete_verify',
    component: DeleteVerify,
  },
  {
    path: '/delete/partner',
    name: 'delete_partner',
    component: DeletePartner,
  },
  {
    path: '/delete/partners',
    name: 'delete_partners',
    component: DeletePartners,
  },
  {
    path: '/delete/partners/verify',
    name: 'delete__partners-verify',
    component: PartnersVerify,
  },
  {
    path: '/delete/partners/done',
    name: 'delete__partners-done',
    component: PartnersDone,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
