import { createStore } from 'vuex';

export default createStore({
  state: {
    user: [
      {
        number: '',
        status: '',
        id: '',
        connected: '',
      },
    ],
  },
  getters: {
  },
  mutations: {
    signIn(
      state,
      {
        number, status, id, connected,
      },
    ) {
      state.user.push({
        number,
        status,
        id,
        connected,
      });
    },
  },
  actions: {
  },
  modules: {
  },
});
