<template>
  <div>
    <CheckMarried v-if="this.users[this.users.length - 1].status === 'В браке'"/>
    <CheckRelationships v-else-if="this.users[this.users.length - 1].status === 'В отношениях'"/>
    <CheckSingle v-else/>
  </div>
</template>

<script>
import CheckMarried from '@/components/CheckMarried.vue';
import CheckRelationships from '@/components/CheckRelationships.vue';
import CheckSingle from '@/components/CheckSingle.vue';

export default {
  data() {
    return {
      users: this.$store.state.user,
    };
  },
  components: { CheckMarried, CheckRelationships, CheckSingle },
};

</script>
